import Colors from "./Colors";

const styles = {
  searchField: {
    backgroundColor: "#F5F5F5",
    width: "calc(100% - 5px)",
    boxSizing: "content-box"
  },
  App: {
    padding: "32px"
  },
  underlineDark: {
    borderColor: "#B0BEC5"
  },
  underlineDarkFocused: {
    borderColor: "#90A4AE"
  },
  results: { margin: "60px auto", maxWidth: "800px" },
  container: {
    maxWidth: "1280px",
    margin: "62px auto"
  },
  header: {
    textAlign: "center",
    padding: "32px"
  },
  heading: {
    color: Colors.fontDark,
    fontSize: "52px",
    margin: 0,
    fontWeight: "300"
  },
  heading2: {
    color: Colors.fontDark,
    fontSize: "38px",
    margin: "32px 0",
    fontWeight: "300"
  },
  subHeading: {
    color: Colors.fontLight,
    fontSize: "28px"
  },
  tag: {
    backgroundColor: "#B0BEC5",
    padding: "6px",
    borderRadius: "6px",
    color: "#37474F",
    fontSize: "14px",
    display: "inline-block "
  }
};
export default styles;



