import React, { Component } from "react";
import { observer } from "mobx-react";
import Store from "./Store";
import Styles from "./Styles";
import Form from "./Form";
import GeneForm from "./GeneForm";
import TextField from "material-ui/TextField";
import DropDownMenu from "material-ui/DropDownMenu";
import MenuItem from "material-ui/MenuItem";

const VARIANT = "VARIANT";
const GENE = "GENE";
const CELL_LINE = "CELL LINE";
const regexes = [
  { pattern: /^rs\d+$/, name: "dbSNP" },
  { pattern: /^GO:\d{7}$/, name: "GO" },
  { pattern: /^((HGNC|hgnc):)?\d{1,5}$/, name: "HGNC" },
  {
    pattern: /^((AC|AP|NC|NG|NM|NP|NR|NT|NW|XM|XP|XR|YP|ZP)_\d+|(NZ\_[A-Z]{4}\d+))(\.\d+)?/,
    name: "RefSeq"
  }
];

//REfSeq 	NG_007400.1:g.8638G>T

@observer
class EBISC extends Component {
  handleChange = (event, index, value) => {
    Store.userSelection = value;
  };

  async handleInput(e, value) {
    Store.inputType = "";
    //console.log(value);
    regexes.forEach(x => {
      if (value.trim().match(x.pattern)) {
        console.log("MATCHED WITH: " + x.name);
        Store.inputType = x.name;
        return;
      }
    });

    // checking for HGNC
    if (value.length >= 3) {
      try {
        let result = await Store.fetchStuff(
          "https://rest.ensembl.org/lookup/symbol/homo_sapiens/" +
            value.trim() +
            "?content-type=application/json"
        );
        if (result.id) {
          Store.inputType = "Gene Symbol";
        }
      } catch (e) {
        console.log(e);
      }
    }

    // checking for HGVS
    /*
    if (value.length >= 3) {
      try {
        let result = await Store.fetchStuff(
          "https://rest.ensembl.org//vep/human/hgvs/" +
            value.trim() +
            "?content-type=application/json"
        );
        if (result.id) {
          Store.inputType = "HGVS";
        }
      } catch (e) {
        console.log(e);
      }
    }

    */
  }

  render() {
    return (
      <div
        style={{
          padding: "32px",
          margin: "32px 0"
        }}>
        <div style={{ display: "flex" }}>
          <TextField
            name="userInput"
            onChange={this.handleInput}
            underlineStyle={{ display: "none" }}
            style={{
              padding: "8px 12px",
              borderWidth: 0,
              border: "2px solid #333",
              backgroundColor: "#fff",
              fontSize: "22px",
              color: "#ededed",
              width: "100%"
            }}
            defaultValue="rs123456"
          />
          {/*
          <DropDownMenu
            style={{
              backgroundColor: "#fff",
              margin: "16px",
              fontSize: "20px"
            }}
            value={Store.userSelection}
            onChange={this.handleChange}>
            <MenuItem
              style={{
                fontSize: "22px"
              }}
              value={VARIANT}
              primaryText={VARIANT}
            />
            <MenuItem
              style={{
                fontSize: "22px"
              }}
              value={GENE}
              primaryText={GENE}
            />
            <MenuItem
              style={{
                fontSize: "22px"
              }}
              value={CELL_LINE}
              primaryText={CELL_LINE}
            />
          </DropDownMenu>


        */}
        </div>

        {Store.inputType !== "" && (
          <span
            style={{
              background: "#7A7A7A",
              padding: "8px",
              margin: "8px 0",
              color: "#e2e2e2",
              borderRadius: "5px",
              display: "inline-block"
            }}>
            {Store.inputType}
          </span>
        )}

        {Store.inputType !== "" && (
          <span
            style={{
              background: "#7A7A7A",
              padding: "8px",
              margin: "8px 0",
              color: "#e2e2e2",
              borderRadius: "5px",
              display: "inline-block",
              float: "right"
            }}>
            {Store.inputCategory}
          </span>
        )}
        {Store.inputCategory === "Variants" && <Form />}
        {Store.inputCategory === "Genes" && <GeneForm />}
      </div>
    );
  }
}
export default EBISC;



